*{margin:0px;text-decoration:none;list-style-type:none;color:#fff}body{font-size:18px;font-family:"Oswald",sans-serif;background-color:transparent;overflow-x:hidden;scrollbar-width:none}body::-webkit-scrollbar{display:none}#animatedBackground{position:absolute;top:0;left:-200px;z-index:-1;height:143vh;width:calc(100vw + 400px);background-repeat:repeat;background-size:cover;background:-webkit-repeating-linear-gradient(45deg, #000000 0%, #00008a 2%, #000070 2%, #000000 6%);background:repeating-linear-gradient(45deg, #000000 0%, #00008a 2%, #000070 2%, #000000 6%)}
.experienceContainer{height:90vh;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.experienceContainer .expCard{background:#050505;box-shadow:0px 0px 10px 2px #2f3df8;border-radius:85px;position:relative;width:320px;height:400px;display:flex;flex-direction:column;align-items:center}.experienceContainer .expCard h1{margin-top:20px;text-align:center;margin-bottom:10px}.experienceContainer .expCard .underline{height:2px;background:rgba(47,61,248,.5);width:80%}.experienceContainer .expCard p{margin:10%;font-size:10px}.experienceContainer .expCard p li{margin-left:-20px}.experienceContainer .expCard p li::before{content:"";position:relative;right:15px;top:-5px;width:6px;height:6px;display:inline-block;box-shadow:0px 0px 10px 1px rgba(47,61,248,.8);border-radius:50%;color:#050505}.experienceContainer .expCard.card1{top:-100%;z-index:5}.experienceContainer .expCard.card2{top:-60%;z-index:4}.experienceContainer .expCard.card3{top:-20%;z-index:3}.experienceContainer .expCard.card4{top:20%;z-index:2}.experienceContainer .expCardBox{position:relative;top:-120vh;z-index:10;max-width:550px}.experienceContainer .expCard,.experienceContainer .expCardBox{margin:0px auto}.experienceContainer .expCardBig{position:absolute;z-index:50 !important;top:15vh !important;height:80vh;width:40vw;left:30vw}.experienceContainer .expCardBig h1{font-size:3rem}.experienceContainer .expCardBig p{font-size:1.2rem}
.projectsContainer{padding:2% 0%;display:grid;grid-template-columns:1fr 1fr}.projectsContainer .projectCard{display:flex;flex-direction:column;padding:30px;padding-bottom:50px;margin:3% 8%;min-height:35vh;background:#050505;box-shadow:0px 0px 40px 10px #2f3df8}.projectsContainer .projectCard>*{margin-bottom:10px}.projectsContainer .underline{height:2px;background:rgba(47,61,248,.5)}.projectsContainer ul{list-style:none}.projectsContainer li::before{content:"";position:relative;right:15px;top:-5px;width:6px;height:6px;display:inline-block;box-shadow:0px 0px 10px 5px #2f3df8;border-radius:50%;color:#050505}.projectsContainer li.li-2::before{content:"";position:relative;right:15px;top:-5px;width:6px;height:6px;display:inline-block;box-shadow:0px 0px 10px 5px red;border-radius:50%;color:#050505}
.contactContainer{height:90vh;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.contactContainer .icon{height:140px;width:140px;background:#050505;box-shadow:0px 0px 40px 10px #2f3df8;transition:.1s;display:flex;justify-content:center}.contactContainer .icon:hover{-webkit-transform:scale(1.05);transform:scale(1.05);box-shadow:0px 0px 60px 1px rgba(47,61,248,.9)}.contactContainer .dialogBox{position:absolute;top:10%;width:10vw;min-width:200px;height:100px;left:45vw;-webkit-transform:translateY(-50px);transform:translateY(-50px);opacity:0;transition:all .5s ease;background:rgba(47,61,248,.1);border-radius:10%;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);display:flex;justify-content:center;align-items:center}.contactContainer .dialogBox.active{opacity:1;-webkit-transform:translateY(0px);transform:translateY(0px)}
.aboutContainer{height:90vh;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.aboutContainer .icon{height:140px;width:140px;background:#050505;box-shadow:0px 0px 40px 10px #2f3df8;display:flex;justify-content:center;transition:.1s,opacity .5s ease}.aboutContainer .icon:hover{-webkit-transform:scale(1.05);transform:scale(1.05);box-shadow:0px 0px 60px 1px rgba(47,61,248,.9)}.aboutContainer .icon.inactive{opacity:0}.aboutContainer .preview{height:90vh;position:absolute;top:20px;z-index:-1}.aboutContainer .preview.active{z-index:10}.aboutContainer .preview.active .iconBubble,.aboutContainer .preview.active .descBubble{opacity:1;-webkit-transform:translateY(0%);transform:translateY(0%)}.aboutContainer .preview .iconBubble,.aboutContainer .preview .descBubble{background:#050505;border-radius:50%;position:relative;margin-top:2vh;-webkit-transform:translateY(20%);transform:translateY(20%);opacity:0}.aboutContainer .preview .iconBubble{box-shadow:-10px 0px 20px 1px #2f3df8;top:40vh;height:20vh;width:20vh;padding:5vh;display:flex;align-items:center;justify-content:center;z-index:5;transition:all .8s ease}.aboutContainer .preview .iconBubble img{height:70%;width:70%}.aboutContainer .preview .descBubble{height:70vh;width:20vw;padding:2vh calc(2vh + 100px);top:-30vh;right:-10vw;display:flex;flex-direction:column;justify-content:start;align-items:center;box-shadow:0px 0px 40px 10px #2f3df8;z-index:4;transition:all .6s ease;font-size:1.05em}.aboutContainer .preview .descBubble h1{margin-top:10vh;margin-bottom:10px}.aboutContainer .preview .descBubble .underline{height:2px;background:rgba(47,61,248,.5);width:60%;margin-bottom:20px}
.home{width:100vw;height:80vh}.home .slick-track{display:flex;padding:30px;grid-gap:3rem;gap:3rem}.home .slick-slide{display:flex;justify-content:center;margin-bottom:1;outline:none}.home .slick-list{overflow:hidden}.home .card{width:90vw !important;height:75vh;min-height:650px;max-height:100vh;display:flex !important;flex-direction:row;justify-content:center;font-size:3rem;font-family:"Quintessential",cursive;padding:0px 40px;margin:30px 0px;box-shadow:0px 5px 40px 5px rgba(0,0,0,.9);background-color:rgba(0,0,0,.8);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px)}.home .card .cardImgBoxVer{max-height:650px;background-color:#000}.home .card .cardImgBoxVer img{width:auto;height:550px}.home .card .cardImgBoxHor{height:450px;width:670px}.home .card .cardImgBoxHor img{width:inherit;height:inherit}.home .card .descriptionBox{width:50%;display:flex;flex-direction:column;justify-content:center}.home .card .cardImgBoxHor,.home .card .cardImgBoxVer,.home .card .descriptionBox{margin:100px 30px}.home .card .cardImgBoxHor,.home .card .cardImgBoxVer{box-shadow:0px 0px 80px 50px #000}.home .card .descriptionBox,.home .card .cardImgBox{padding:0% 3%}
header,footer{box-shadow:0px 5px 80px 5px rgba(47,61,248,.4);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background-color:rgba(5,5,5,.7);padding:0px 10px}header{width:100vw;height:8vh;padding:15px}header nav{height:100%}header nav ul{height:100%;display:flex;flex-direction:row;justify-content:space-around;align-items:flex-end;list-style:none;font-size:2rem}header nav ul .homeButton{font-family:"Quintessential",cursive;font-size:2rem;width:150px;text-align:center;white-space:pre-line;line-height:36px}footer{width:100vw;height:15vh;padding:15px;display:flex;flex-direction:row;justify-content:space-evenly;position:absolute;top:124vh}footer .iconsC{opacity:.2;font-size:.8rem}footer .col1,footer .col2{height:100%;display:flex;justify-content:space-between;flex-direction:column}footer .col1 li,footer .col2 li{margin:8px 0px}footer .col1 .icons,footer .col2 .icons{display:flex;justify-content:space-between}footer .col2{text-align:center}footer a img{height:32px;width:32px}main{min-height:100vh}
